<template>
  <van-page>
    <van-nav-bar title="HMAC SHA-256" />
    <van-cell-group>
      <!-- Input key -->
      <van-field v-model="key" label="Key" placeholder="Enter secret key..." type="textarea" rows="2" />

      <!-- Input message -->
      <van-field v-model="message" label="Message" placeholder="Enter message to hash..." type="textarea" rows="5" />

      <!-- Calculate HMAC button -->
      <div class="button-container">
        <van-button type="primary" @click="calculateHMAC">Calculate HMAC</van-button>
      </div>

      <!-- Display message -->
      <van-field v-model="message" label="Message" type="textarea" readonly>
        <template #button>
          <van-button size="mini" @click="copyMessageToClipboard">Copy Message</van-button>
        </template>
      </van-field>

      <!-- Display HMAC -->
      <van-field v-model="hmac" label="HMAC Value" type="textarea" readonly>
        <template #button>
          <van-button size="mini" @click="copyHMACToClipboard">Copy HMAC</van-button>
        </template>
      </van-field>
    </van-cell-group>

    <van-divider />

    <!-- Scene description -->
    <van-cell>
      <div style="width: 100%;">
        <span style="font-weight: bold;">Use Cases of HMAC</span>
        <div class="use-cases">
          1. Verify both authenticity and integrity of a message;<br />
          2. Secure API requests by attaching an HMAC to ensure the request hasn’t been tampered with.
        </div>
      </div>
    </van-cell>

    <van-divider />
  </van-page>
  <div></div>
  <div></div>
  <div></div>
  <div></div>
  <div></div>
  <div></div>
  <div></div>

  <TabBar />
</template>

<script>
import { ref } from 'vue';
import TabBar from '@/components/TabBar.vue'; // 确保路径正确
import CryptoJS from 'crypto-js'; // 导入crypto-js

export default {
  name: 'HMACPage',
  components: {
    TabBar,
  },
  setup() {
    const key = ref('');
    const message = ref('');
    const hmac = ref('');

    const calculateHMAC = () => {
      if (!key.value.trim() || !message.value.trim()) {
        alert('Please enter both a key and a message.');
        return;
      }

      // 使用crypto-js计算HMAC-SHA256
      hmac.value = CryptoJS.HmacSHA256(message.value.trim(), key.value.trim()).toString(CryptoJS.enc.Hex);
    };

    const copyMessageToClipboard = () => {
      navigator.clipboard.writeText(message.value).then(() => {
        alert('Message copied');
      });
    };

    const copyHMACToClipboard = () => {
      navigator.clipboard.writeText(hmac.value).then(() => {
        alert('HMAC copied');
      });
    };

    return {
      key,
      message,
      hmac,
      calculateHMAC,
      copyMessageToClipboard,
      copyHMACToClipboard,
    };
  },
};
</script>

<style scoped>
.van-page {
  padding: 16px;
}

.use-cases {
  text-align: left;
}
</style>